import {Component, OnInit} from '@angular/core';


@Component({
  selector: 'app-admin-layout',
  templateUrl: './admin-layout.component.html',
  styleUrls: ['./admin-layout.component.scss']
})
export class AdminLayoutComponent implements OnInit {

  constructor() {

  }

  isDarkMode = false;
  isMiniMode = false;
  isShowPluginFeature = false;
  isShowSideBar = false;
  colors = [
    {
      name: 'white',
      value: 'var(--white)',
      isImage: false
    },
    {
      name: 'red',
      value: 'var(--red)',
      isImage: false
    },
    {
      name: 'blue',
      value: 'var(--blue)',
      isImage: false
    },
    {
      name: 'yellow',
      value: 'var(--yellow)',
      isImage: false
    },
  ];
  colorMode: string | null = '';
  colorName: string | null = '';
  localizes = [
    {
      code: 'vn',
      name: 'Tiếng Việt',
      icon: '/assets/icons/vietnam.svg',
    },
    {
      code: 'us',
      name: 'English',
      icon: '/assets/icons/united-states.svg',
    },
  ];
  localize = 'vn';

  ngOnInit(): void {
    if (localStorage.getItem('isDarkMode') === 'true') {
      this.isDarkMode = true;
    } else {
      this.isDarkMode = false;
    }
    if (localStorage.getItem('isMiniMode') === 'true') {
      this.isMiniMode = true;
    } else {
      this.isMiniMode = false;
    }
    this.colorMode = localStorage.getItem('color');
    this.colorName = localStorage.getItem('colorName');
  }

  // tslint:disable-next-line:typedef
  togglePlugin() {
    this.isShowPluginFeature = !this.isShowPluginFeature;
  }

  // tslint:disable-next-line:typedef
  changeLogoMode() {
    this.isDarkMode = !this.isDarkMode;
    localStorage.setItem('isDarkMode', this.isDarkMode ? 'true' : '');
  }

  // tslint:disable-next-line:typedef
  changeSideBarMode() {
    this.isMiniMode = !this.isMiniMode;
    localStorage.setItem('isMiniMode', this.isMiniMode ? 'true' : '');
  }

  // tslint:disable-next-line:typedef
  showSideBar() {
    const sidebar = document.getElementById('sidebar');
    // @ts-ignore
    // tslint:disable-next-line:max-line-length
    if (sidebar.style.opacity === '' || sidebar.style.visibility === '' || (sidebar.style.opacity === '0' && sidebar.style.visibility === 'hidden')) {
      // @ts-ignore
      sidebar.style.opacity = '1';
      // @ts-ignore
      sidebar.style.visibility = 'visible';
      // @ts-ignore
      sidebar.style.marginLeft = '0';
      this.isShowSideBar = true;
    } else {
      // @ts-ignore
      sidebar.style.opacity = '0';
      // @ts-ignore
      sidebar.style.visibility = 'hidden';
      // @ts-ignore
      sidebar.style.marginLeft = '-280px';
      this.isShowSideBar = false;
    }
  }

  // tslint:disable-next-line:typedef
  storeColor(value: string, name: string) {
    const sidebar = document.getElementById('sidebar');
    // @ts-ignore
    localStorage.setItem('color', value);
    localStorage.setItem('colorName', name);
  }

  // tslint:disable-next-line:typedef
  changeLocalize(localize: any) {
    localize = '';
  }
}
